import React from 'react';

const Landing = props => {
  return (
    <div className="container">
      <h1>Привет! Добро пожаловать в Дыбр.</h1>
      <p>
        Это тестовая версия новых дневников. Можно зарегистрироваться и посмотреть, как продвигаются дела. Но тут еще мало чего работает.{' '}
      </p>
    </div>
  );
};

export default Landing;



